/* --------------------------------------------------------------------------------------------- */
/* ----------------------------------------- STEPS-FORM ---------------------------------------- */
/* --------------------------------------------------------------------------------------------- */

.steps-form,
.steps-form-2 {
    display: table;
    width: 100%;
    position: relative;
}

.steps-form-3 {
    width: 2px;
    height: 470px;
    position: relative;
}

/* --------------------------------------------------------------------------------------------- */
/* ------------------------------------ STEPS-FORM STEPS-ROW ----------------------------------- */
/* --------------------------------------------------------------------------------------------- */

.steps-form .steps-row,
.steps-form-2 .steps-row-2 {
    display: table-row;
}

.steps-form-3 .steps-row-3 {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-flex-direction: column;
    flex-direction: column;
}

.steps-form .steps-row:before,  {
    top: 14px;
    bottom: 0;
    position: absolute;
    content: " ";
    width: 100%;
    height: 1px;
    background-color: #ccc;
}

.steps-form-2 .steps-row-2:before {
    top: 14px;
    bottom: 0;
    position: absolute;
    content: " ";
    width: 100%;
    height: 2px;
    background-color: #7283a7;
}

.steps-form-3 .steps-row-3:before {
    top: 14px;
    bottom: 0;
    position: absolute;
    content: "";
    width: 2px;
    height: 100%;
    background-color: #7283a7;
}

/* --------------------------------------------------------------------------------------------- */
/* ------------------------------- STEPS-FORM STEPS-ROW STEPS-STEP ----------------------------- */
/* --------------------------------------------------------------------------------------------- */
    
.steps-form .steps-row .steps-step,
.steps-form-2 .steps-row-2 .steps-step-2 {
    display: table-cell;
    text-align: center;
